'use client';
import React, { useState } from 'react';
import {
  Box,
  VStack,
  HStack,
  Input,
  Textarea,
  Button,
  Text,
  Field,
} from '@chakra-ui/react';

interface FormData {
  jobTitle: string;
  firstName: string;
  lastName: string;
  phone: string;
  email: string;
  message: string;
}

interface FormErrors {
  jobTitle?: string;
  firstName?: string;
  lastName?: string;
  phone?: string;
  email?: string;
}

const ConnectForm: React.FC = () => {
  const [formData, setFormData] = useState<FormData>({
    jobTitle: '',
    firstName: '',
    lastName: '',
    phone: '',
    email: '',
    message: '',
  });

  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const isMobile = useBreakpointValue({ base: true, lg: false });

  const validateForm = (): boolean => {
    const newErrors: FormErrors = {};

    if (!formData.jobTitle.trim()) {
      newErrors.jobTitle = 'Job title is required';
    }

    if (!formData.firstName.trim()) {
      newErrors.firstName = 'First name is required';
    }

    if (!formData.lastName.trim()) {
      newErrors.lastName = 'Last name is required';
    }

    if (!formData.phone.trim()) {
      newErrors.phone = 'Phone number is required';
    }

    if (!formData.email.trim()) {
      newErrors.email = 'Email is required';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = 'Email is invalid';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleInputChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));

    // Clear error when user starts typing
    if (errors[name as keyof FormErrors]) {
      setErrors((prev) => ({
        ...prev,
        [name]: undefined,
      }));
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    setIsSubmitting(true);

    try {
      // Simulate API call
      await new Promise((resolve) => setTimeout(resolve, 2000));

      // toaster({
      //   title: 'Message sent successfully!',
      //   description: 'We will get back to you soon.',
      //   status: 'success',
      //   duration: 5000,
      //   isClosable: true,
      // });

      // Reset form
      setFormData({
        jobTitle: '',
        firstName: '',
        lastName: '',
        phone: '',
        email: '',
        message: '',
      });
    } catch (error) {
      // toast({
      //   title: 'Error sending message',
      //   description: 'Please try again later.',
      //   status: 'error',
      //   duration: 5000,
      //   isClosable: true,
      // });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Box
      maxW='600px'
      mx='auto'
      p={8}
      bg={'transparent'}
      backdropFilter='blur(11px)'
      rounded={30}
      boxShadow='0 8px 288px #4690FF88'
      position='relative'
      overflow='hidden'
      _before={{
        content: '""',
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        zIndex: -1,
      }}
    >
      <Text
        fontSize='2xl'
        fontWeight='bold'
        color='white'
        mb={6}
        textAlign='left'
      >
        Connect with us
      </Text>

      <Box as='form' onSubmit={handleSubmit}>
        <VStack gap={4}>
          {/* Job Title */}
          <Field.Root>
            <Input
              name='jobTitle'
              value={formData.jobTitle}
              onChange={handleInputChange}
              placeholder='What is your job title?'
              size='xl'
              bg='rgba(0, 0, 0, 0.5)'
              border='none'
              borderRadius='12px'
              color='white'
              _placeholder={{ color: 'rgba(255, 255, 255, 0.6)' }}
              _focus={{
                boxShadow: '0 0 0 2px rgba(255, 255, 255, 0.3)',
                bg: 'rgba(0, 0, 0, 0.6)',
              }}
            />
          </Field.Root>
          {/* Name and Last Name */}
          <HStack gap={4} w='full'>
            <Field.Root>
              <Input
                name='firstName'
                value={formData.firstName}
                onChange={handleInputChange}
                placeholder='Name'
                size='lg'
                bg='rgba(0, 0, 0, 0.5)'
                border='none'
                borderRadius='12px'
                color='white'
                _placeholder={{ color: 'rgba(255, 255, 255, 0.6)' }}
                _focus={{
                  boxShadow: '0 0 0 2px rgba(255, 255, 255, 0.3)',
                  bg: 'rgba(0, 0, 0, 0.6)',
                }}
              />
            </Field.Root>
            <Field.Root>
              <Input
                name='lastName'
                value={formData.lastName}
                onChange={handleInputChange}
                placeholder='Last Name'
                size='lg'
                bg='rgba(0, 0, 0, 0.5)'
                border='none'
                borderRadius='12px'
                color='white'
                _placeholder={{ color: 'rgba(255, 255, 255, 0.6)' }}
                _focus={{
                  boxShadow: '0 0 0 2px rgba(255, 255, 255, 0.3)',
                  bg: 'rgba(0, 0, 0, 0.6)',
                }}
              />
            </Field.Root>
          </HStack>

          {/* Phone and Email */}
          <HStack gap={4} w='full'>
            <Field.Root>
              <Input
                name='phone'
                value={formData.phone}
                onChange={handleInputChange}
                placeholder='Phone'
                size='lg'
                bg='rgba(0, 0, 0, 0.5)'
                border='none'
                borderRadius='12px'
                color='white'
                _placeholder={{ color: 'rgba(255, 255, 255, 0.6)' }}
                _focus={{
                  boxShadow: '0 0 0 2px rgba(255, 255, 255, 0.3)',
                  bg: 'rgba(0, 0, 0, 0.6)',
                }}
              />
            </Field.Root>
            <Field.Root>
              <Input
                name='email'
                type='email'
                value={formData.email}
                onChange={handleInputChange}
                placeholder='Email'
                size='lg'
                bg='rgba(0, 0, 0, 0.5)'
                border='none'
                borderRadius='12px'
                color='white'
                _placeholder={{ color: 'rgba(255, 255, 255, 0.6)' }}
                _focus={{
                  boxShadow: '0 0 0 2px rgba(255, 255, 255, 0.3)',
                  bg: 'rgba(0, 0, 0, 0.6)',
                }}
              />
            </Field.Root>
          </HStack>

          {/* Message */}
          <Field.Root>
            <Textarea
              name='message'
              value={formData.message}
              onChange={handleInputChange}
              placeholder='Your message (optional)'
              size='lg'
              minH='120px'
              bg='rgba(0, 0, 0, 0.5)'
              border='none'
              borderRadius='12px'
              color='white'
              resize='vertical'
              _placeholder={{ color: 'rgba(255, 255, 255, 0.6)' }}
              _focus={{
                boxShadow: '0 0 0 2px rgba(255, 255, 255, 0.3)',
                bg: 'rgba(0, 0, 0, 0.6)',
              }}
            />
          </Field.Root>

          {/* Submit Button */}
          <Button
            type='submit'
            size='lg'
            w='full'
            bg={'red'}
            borderRadius='10px'
            border='none'
            fontSize='lg'
            py={6}
            height={'60px'}
            loading={isSubmitting}
            loadingText='Sending...'
            _hover={{
              bg: 'light',
              color: 'primary',
            }}
            _active={{
              transform: 'translateY(0)',
            }}
            transition='all 0.2s'
          >
            Send Message
          </Button>
        </VStack>
      </Box>
    </Box>
  );
};

export default ConnectForm;
